@media all and (max-width: 1200px) {
    $mainColor: rgba(0,0,0,.15);
    $linkColor: #279BF1;
    .container {
        width: 100%;
    }
    .page-slides__left-side {
        top: 882px;
    }
    .header-menu {
        height: 500px;
        position: absolute;
        overflow: hidden;
        box-shadow: 2px 2px 5px 2px rgba(0,0,0,.5);
        border-radius: 10px;
    }
    .header-navigation {
            display: flex;
            flex-direction: column;
            width: 100%;
        li:not(:last-child) {
            transform: translateY(40%);
            height: 50px;
            width: 100%;
            display: block;
            margin: 10px;
          a {
            font-family: 'Verdana';
            text-align: start;
            cursor: pointer;
          }
          a:hover{color: $linkColor;}
          font-size: 17px;
        }
        li:last-child {
          height: 100%;
          width: 100%;
          display: block;
          background: none;
          margin: 20px 10px 0 ;
          a {
            width: 50px;
            display: block;
            float: none;
            cursor: pointer;
            color: black;
            }
        }
    }
    .header-inf-container {
        height: 100%;
    }
    .header-information {
        padding: 0 20px;
        margin: 10px 0;
        display: flex;
        align-items: center;
        list-style: none;
        flex-direction: column;
        text-align: center;
        height: 100%;
    }
    
.we-are__contacts {
    flex-direction: column;
    height: 400px;
  }
  .we-are__map {
    width: 100%;
    height: 320px;
  }
  .we-are__numbers {
    width: 100%;
    height: 80px;
    ul {
      flex-direction: row;
      justify-content: space-around;
      align-items: center;
      padding: 0;
    }
    li {
      width: max-content;
      height: max-content;
      padding: 0;
    }
  }
}
// -------------------------------------------------------

@media all and (max-width: 1000px) { 
    .part-of-catalog-container {
        flex-direction: column;
    }
    .catalog-elements-grid {
        grid-row-gap: 20px;
    }
    .catalog__element {
        &:nth-of-type(1) {
          grid-column-start: 1;
          grid-column-end: 2;
          grid-row-start: 1;
          grid-row-end: 2;
        }
        &:nth-of-type(2) {
          grid-column-start: 2;
          grid-column-end: 3;
          grid-row-start: 1;
          grid-row-end: 2;
        }
        &:nth-of-type(3) {
          grid-column-start: 1;
          grid-column-end: 2;
          grid-row-start: 2;
          grid-row-end: 3;
        }
        &:nth-of-type(4) {
            grid-column-start: 2;
            grid-column-end: 3;
            grid-row-start: 2;
            grid-row-end: 3;
        }
        &:nth-of-type(5) {
            grid-column-start: 1;
            grid-column-end: 2;
            grid-row-start: 3;
            grid-row-end: 4;
        }
        &:nth-of-type(6) {
            grid-column-start: 2;
            grid-column-end: 3;
            grid-row-start: 3;
            grid-row-end: 4;
        }
    }
}

// -------------------------------------------------------
@media all and (max-width: 800px) { 
    .page-slides__left-side {
        position: absolute;
    }
    .we-are__contacts {
        flex-direction: column;
        height: 530px;
    }
    .we-are__numbers {
        width: 100%;
        height: 200px;
        ul {
            width: 100%;
            height: 200px;
            flex-direction: column;
            justify-content: space-around;
            align-items: center;
            padding: 0;
        }
        li {
            text-align: center;
        }
    }
    .catalog-elements-grid {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr 1fr;
    }
    .catalog__element {
        &:nth-of-type(1) {
            grid-column-start: 1;
            grid-column-end: 2;
            grid-row-start: 1;
            grid-row-end: 2;
        }
        &:nth-of-type(2) {
            grid-column-start: 1;
            grid-column-end: 2;
            grid-row-start: 2;
            grid-row-end: 3;
        }
        &:nth-of-type(3) {
            grid-column-start: 1;
            grid-column-end: 2;
            grid-row-start: 3;
            grid-row-end: 4;
        }
        &:nth-of-type(4) {
            grid-column-start: 1;
            grid-column-end: 2;
            grid-row-start: 4;
            grid-row-end: 5;
        }
        &:nth-of-type(5) {
            grid-column-start: 1;
            grid-column-end: 2;
            grid-row-start: 5;
            grid-row-end: 6;
        }
        &:nth-of-type(6) {
            grid-column-start: 1;
            grid-column-end: 2;
            grid-row-start: 6;
            grid-row-end: 7;
        }
    }
}
@media all and (max-width: 650px) {
    .part-table {
        width: 320px;
        height: max-content;
        p {
            width: 100%;
            padding: 6px 0;
            text-align: center;
            &:nth-of-type(4n-5) {
                background: rgba(0, 0, 0, 0.3);
                border-top-left-radius: 10px;
                border-bottom-left-radius: 0;
                border-top-right-radius: 10px;
                border-bottom-right-radius: 0;
            }
            &:nth-of-type(4n-4) {
                background: rgba(0, 0, 0, 0.3);
                border-top-left-radius: 0;
                border-bottom-left-radius: 10px;
                border-top-right-radius: 0;
                border-bottom-right-radius: 10px;
            }
        }
    }
    .part-price {
        width: 320px;
    }
}
@media all and (max-width: 600px) { 
    .header-section {
        height: 500px;
    }
    .header-inf-container {
        height: 500px;
        position: absolute;
        top: 55px;
        z-index: 0;
        opacity: 0.8;
        border-radius: 10px;
    }
    .wave-js {
        z-index: 50;
    }
    .header-navigation {
        z-index: 50;
        background: white;
        border: 1px solid rgba(0,0,0,.15);
    }
    .header-information {
        justify-content: space-around;
    }
    .page-slides__left-side {
        top: 635px;
    }
    .page-slides__right-side {
        text-align: center;
        padding: 5px 0;
        p, a, span {
            font-size: 15px;
        }
        h2, h3
        {
            margin: 20px 0;
        }
        ul {
            padding: 0;
        }
        li {
            padding: 0;
            display: block;
            margin: 0 auto;
            width: 92%;
            list-style: none;
        } 
        li a,
        li span{
            position: relative;
        }
        li span::before,
        li a::before {
            content: '\2022';
            position: absolute;
            display: block;
            width: 20px;
            height: 20px;
            left: -20px;
            z-index: 10;
        }
    }
    footer {
        h4,
        a,
        span,
        p {
          text-align: center;
        }
    }
}