.waves {
  position: absolute;
  width: 35px;
  height: 35px;
  top: 2.5px;
  left: 125px; }

.waves::after,
.wave-js::after {
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  width: 75%;
  height: 75%;
  content: '';
  transform: translate(-50%, -50%);
  animation-name: bullet-waves;
  animation-duration: 2s;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
  opacity: 0;
  border: 1px solid black;
  border-radius: 100%; }

@keyframes bullet-waves {
  0% {
    width: 75%;
    height: 75%;
    opacity: 1; }
  50% {
    width: 125%;
    height: 125%;
    opacity: 0; }
  100% {
    width: 75%;
    height: 75%; } }

