.catalog-elements-grid {
  width: 100%;
  display: grid;
  background: white;
  margin: 20px 0;
  grid-row-gap: 20px;
}
.catalog__element {
  padding: 20px 0;
  width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  margin: 0 auto;
  border-radius: 10px;
  box-shadow: 3px 3px 5px 3px rgba(0, 0, 0, 0.5);
  &:nth-of-type(1) {
    grid-column-start: 1;
    grid-column-end: 2;
  }
  &:nth-of-type(2) {
    grid-column-start: 2;
    grid-column-end: 3;
  }
  &:nth-of-type(3) {
    grid-column-start: 3;
    grid-column-end: 4;
  }
  h4 {
    font-family: 'Verdana';
    font-size: 18px;
    font-weight: 600;
    text-align: center;
  }
  strong {
    font-family: 'Verdana';
    font-size: 16px;
    font-weight: 600;
  }
  span {
    font-family: 'Verdana';
    font-size: 16px;
  }
  button,a {
    text-align: center;
    font-family: 'Verdana';
    font-size: 16px;
    width: 200px;
    margin: 10px 0;
    padding: 5px 0;
    background: none;
    border: none;
    border-radius: 10px;
    box-shadow: 3px 3px 5px 3px rgba(0, 0, 0, 0.5);
    &:hover {
      box-shadow: 3px 3px 5px 3px rgba(255, 217, 0, 0.5);
    }
    &:active {
      transform: scale(0.9);
    }
  }
  button {
    background: gold;
  }
}
.element__image-container {
  display: block;
  width: 200px;
  height: 235px;
}
.imglists {
  display: flex;
  align-items: flex-start;
  justify-content: start;
  flex-direction: row;
  width: 100%;
  height: 100px;
  li {
    list-style: none;
    text-decoration: none;
    width: max-content;
    width: 100px;
  }
  img {
    width: 100%;
    height: 100%;
  }
}

